.am-toast {
  position: fixed;
  width: 100%;
  z-index: 1999;
  font-size: 14 * 0.01rem;
  text-align: center;
}

.am-toast > span {
  max-width: 50%;
}

.am-toast.am-toast-mask {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  left: 0;
  top: 0;
  -webkit-transform: translateZ(0.01rem);
  transform: translateZ(0.01rem);
}

.am-toast.am-toast-nomask {
  position: fixed;
  max-width: 50%;
  width: auto;
  left: 50%;
  top: 50%;
  -webkit-transform: translateZ(0.01rem);
  transform: translateZ(0.01rem);
}

.am-toast.am-toast-nomask .am-toast-notice {
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}

.am-toast-notice-content .am-toast-text {
  min-width: 60 * 0.01rem;
  border-radius: 3 * 0.01rem;
  color: #fff;
  background-color: rgba(58, 58, 58, 0.9);
  line-height: 1.5;
  padding: 9 * 0.01rem 15 * 0.01rem;
}

.am-toast-notice-content .am-toast-text.am-toast-text-icon {
  border-radius: 5 * 0.01rem;
  padding: 15 * 0.01rem 15 * 0.01rem;
}

.am-toast-notice-content .am-toast-text.am-toast-text-icon .am-toast-text-info {
  margin-top: 6 * 0.01rem;
}